        display: none;
    }
}


.stack3d{
	transform-style: preserve-3d;
	transform: perspective(1000px);
	bottom: -40px;

  & > *:not(:first-child){
    position: absolute;
    top: 0;
    left: 0;
  }

  & > *:nth-child(1){
    transform: translateZ(-40px);
  }
  & > *:nth-child(2){
    transform: translateZ(20px);
  }
  & > *:nth-child(3){
    transform: translateZ(40px);
  }
  & > *:nth-child(4){
    transform: translateZ(60px);
  }
  & > *:nth-child(5){
    transform: translateZ(80px);
  }
  & > *:nth-child(6){
    transform: translateZ(100px);
  }


	& .axil-icon-box{
		pointer-events: none;
		&>*{
			pointer-events: all;
		}

		&>.tilt-box{
			min-width: 8rem;
		}
	}

	& .axil-icon-box + .axil-icon-box{
		top:40%;
	}

	& .axil-icon-box + .axil-icon-box + .axil-icon-box{
		top:70%;
	}



}




#contact-form{
  form.wpcf7-form {
    font-family: var(--font-lato);
      & p {
        display: flex;
        width: 100%;
        flex-wrap: wrap; /* Add this to allow items to wrap when needed */
        gap: 1rem;
  
        & label{
          margin-bottom: -0.5rem;
          font-weight: 900;
          font-size: 1.5rem;
          width: 100%;
          flex: 1;
        }
  
        & span {
          display: flex;
          flex-grow: 1;
          min-width: 0; /* This ensures spans can shrink below content size */
          width: 0; /* This helps with equal distribution and prevents overflow */
          gap: 1rem;
          margin-bottom: 0.5rem;
          width: 100%;
  
          & input:not(input[type="submit"]),
          & textarea{
            width: 100%;
            font-size: var(--wp--preset--font-size--medium) !important;
            line-height: 2em;
            font-family: var(--font-lato);
            padding: 0.5rem;
            outline: unset;
            box-shadow: 0 0 10px rgba(0,0,0,0.1);
            border-radius: 1rem;
            border: 1px solid #dddddd;
            padding-left: 1.25rem;
            transition: box-shadow 0.25s ease;
  
            &:focus{
              box-shadow: 0 0 10px rgba(0,0,0,0.2);
            }
          }
  
          & textarea{
            padding-top: 1rem;
            resize: none;
            margin-bottom: 1rem;
          }
  
        }
  
        & input[type="submit"]{
          width: auto;
          padding-left: 4rem;
          padding-right: 4rem;
          box-shadow: 0 0 10px rgba(0,0,0,0.2);
          &:hover{
            box-shadow: 0 0 10px rgba(0,0,0,0.3);
          }
        }
  
        & .wp-block-button__link:hover{
          $accent3: #778DFD;
          background-color: darken($accent3, 15%) !important;
        }
      }
  }
}


