.axil-service {
  margin-top: 60px;
  position: relative;
  background: #ffffff;

  @media only screen and (max-width: 767px) {
    margin-top: 30px;
    background: #f7f7f7;
  }

  &:hover {
    background: transparent;

    &::after {
      opacity: 1;
    }
  }

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    top: 1rem;
    left: -1rem;
    border: 4px solid var(--wp--preset--color--contrast);
    position: absolute;
    opacity: 0.25;
    z-index: -3;
    transition: opacity 0.2s ease-in-out;
    border-radius: 2px;
    transform: translateZ(-100px);
    filter: blur(0.5px);
    transition: all 0.1s ease-in-out;
  }

  &.red-border:hover {
    &::after {
      border-color:  darken(#FF6880, 10%) ;//  #FF6880;
      
    }
  }
  &.green-border:hover {
    &::after {
      border-color:  darken(#4A9E94, 10%) ; // #4A9E94;
    }
  }
  &.lavender-border:hover {
    &::after {
      border-color:  darken(#778dfd, 10%) ; // #778dfd;
    }
  }
  &.indigo-border:hover {
    &::after {
      border-color:  darken(#35359A, 10%) ; // #35359A;
    }
  }

  &::before {
    width: 92%;
    height: 92%;
    left: -20px;
    bottom: -20px;
    background: rgba(0, 2, 72, 0.1);
    content: "";
    position: absolute;
    z-index: -1;
    transform-style: preserve-3d;
    transform: rotateY(-10deg);
    filter: blur(50px);
    opacity: 1;
    transition: all 0.7s;

    @media only screen and (max-width: 767px) {
      display: none;
    }
  }

  .icon-container {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 60px;
    aspect-ratio: 1;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    border-radius: 50%;
    transform: translateZ(50px);
  }

  &.axil-service--blog {
    .inner {
      padding-top: 0px;
      padding-left: 0px;
      padding-right: 0px;
      padding-bottom: 50px;
      position: relative;
      background-color: #ffffff;

      & > *:not(.wp-block-post-featured-image) {
        padding: 0 2.5rem;
      }

      h2 > a {
        color: var(--wp--preset--color--contrast);
      }

      a.wp-block-post-excerpt__more-link {
        background-color: #778DFD;
        color: #fff;
        text-decoration: none;
        padding: 0.5rem 2rem;
        border-radius: 2rem;

        &:hover {
          background-color: #35359A;
        }
      }
    }
  }

  & .inner {
    padding-top: 60px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 60px;
    position: relative;
    background-color: #ffffff;

    @media only screen and (max-width: 767px) {
      padding: 30px 20px;
    }

    @media only screen and (min-width: 992px) and (max-width: 1199px) {
      padding: 34px 20px;
    }

    @media only screen and (min-width: 768px) and (max-width: 991px) {
      padding: 25px 15px;
    }

    &::after {
      background: #ffffff;
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      -webkit-transform: perspective(500px) rotateY(-5deg);
      transform: perspective(500px) rotateY(-5deg);
      opacity: 0;
      transition: all 0.3s;

      @media only screen and (max-width: 767px) {
        background: #f7f7f7;
        transform: perspective(0) rotateY(0deg);
      }
    }

    &::before {
      -webkit-transform: perspective(500px) rotateY(-5deg);
      transform: perspective(500px) rotateY(-5deg);
      position: absolute;
      width: calc(100% - 5px);
      height: calc(100% - 10px);
      top: 32px;
      content: "";
      border: 4px solid #000345;
      z-index: -1;
      right: 32px;
      transition: all 0.4s;
      opacity: 1;
      visibility: hidden;
      box-shadow: 0 0 1px transparent;
      transition-delay: 0.2s;

      @media only screen and (min-width: 992px) and (max-width: 1199px) {
        width: calc(100% - 15px);
        height: calc(100% - 15px);
      }

      @media only screen and (min-width: 768px) and (max-width: 991px) {
        width: calc(100% - 15px);
        height: calc(100% - 15px);
      }

      @media only screen and (max-width: 767px) {
        width: calc(100% - 15px);
        height: calc(100% - 15px);
        display: none;
      }
    }
  }
}

.tilt-box.wp-block-group{
  max-width: 12rem;
  padding: 1.5rem 1rem;
  //transition: background-color 0.5s ease-in-out 0, opacity 1s ease-in-out, transform 1s ease-in-out !important;
  opacity: 0;
  //opacity: 1;
  transform: translateY(100%) scale(0.5);
  position: relative;
  transition: all 0.6s ease-in-out;
  will-change: opacity transform;

  &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: #fff;
      top: 0;
      left: 0;
      z-index: -1;
      border-radius: 0.5rem;
      display: block;
      box-shadow: 0px 0px 24px rgba(33,25,79,0.1);
  }

  &::after{
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      top: 1rem;
      left: -1rem;
      border: 3px solid var(--wp--preset--color--contrast);
      position: absolute;
      opacity: 0;
      z-index: -99;
      transition: opacity 0.2s ease-in-out;
      border-radius: 2px;
      transform: translateZ(-100px);
      filter: blur(0.5px);
  }

  &.red-border:hover {
    &::after {
      border-color:  darken(#FF6880, 10%) ;
    }
  }
  &.green-border:hover {
    &::after {
      border-color:  darken(#4A9E94, 10%) ;//  #4A9E94;
    }
  }
  &.lavender-border:hover {
    &::after {
      border-color:  darken(#778dfd, 10%) ;//  #778dfd;
    }
  }
  &.indigo-border:hover {
    &::after {
      border-color:  darken(#35359A, 10%) ; // #35359A;
    }
  }


  &:hover{
      &::after{
        
          opacity: 1;
      }
  }

